import uniNumberBox from '@/components/uni-number-box/uni-number-box.vue'
import { api } from '@/config/common.js'
import { stockCheck } from '@/api/stkstock.js'
export default {
	components: {
		uniNumberBox
	},
	data() {
		return {
			reqData: {
				orderlist: []
			}
		}
	},
	onLoad(options) {
		for (let item of JSON.parse(options.productList)) {
			let data = {
				productid: item.productid,
				productname: item.productname,
				qty: item.qty,
				currentQty: item.qty,
				status: '帐实相符',
				statusColor: '#808695',
				unit: item.unit,
				ismainunit: 1
			}
			this.reqData.orderlist.push(data)
		}
	},
	computed: {
		headerHeight() {
			return this.$headerHeight
		},
		mainHeight() {
			return this.$mainHeight
		}
	},
	methods: {
		handleNavbarClickLeft() {
			uni.navigateBack({
				delta: 1
			})
		},
		handleQtyChange(val, item) {
			item.qty = val
			if ((item.qty - item.currentQty) > 0) {
				item.status = '盘盈'
				item.statusColor = '#3dc57a'
			} else if ((item.qty - item.currentQty) < 0) {
				item.status = '盘亏'
				item.statusColor = '#f4613d'
			} else {
				item.status = '帐实相符'
				item.statusColor = '#808695'
			}
		},
		handleAdd() {
			uni.navigateBack({
				delta: 1
			})
		},
		handleSubmit() {
			this.$refs.loading.open()
			stockCheck(api.stkStock, this.reqData).then(res => {
				this.$refs.loading.close()
				if (res.status == 200 && res.data.returnCode == '0000') {
					uni.showToast({
						icon: 'success',
						title: '提交成功'
					})
					setTimeout(()=>{
						uni.navigateBack({
							delta: 2
						})
					},500)
				} else {
					uni.showToast({
						icon: 'none',
						title: res.data.returnMessage
					})
				}
			}).catch(error => {
				this.$refs.loading.close()
				uni.showToast({
					icon: 'none',
					title: error
				})
			})
		}
	}
}
